.main-nav {
  position: relative;
  width: auto;
  min-height: 66px;
  background-color: rgba(0, 0, 0, 0.3);
}
@media (min-width: 1200px) {
  .main-nav {
    background-color: transparent;
    width: 100%;
    max-width: 940px;
    margin: 0 auto;
    display: flex;
    justify-content: space-between;
  }
}
.main-nav__list {
  margin: 0;
  padding: 0;
  list-style: none;
}
.main-nav__toggle {
  position: absolute;
  top: 6px;
  right: 0;
  z-index: 2;
  display: block;
  width: 70px;
  height: 50px;
  background-color: transparent;
  cursor: pointer;
  border: none;
}
@media (min-width: 660px) {
  .main-nav__toggle {
    top: 27px;
  }
}
@media (min-width: 1200px) {
  .main-nav__toggle {
    display: none;
  }
}
@media (max-width: 1200px) {
  .main-nav--closed .main-nav__toggle::before {
    content: "";
    position: absolute;
    top: 15px;
    right: 20px;
    width: 50px;
    height: 3px;
    background-color: #FFFFFF;
    box-shadow: 0 10px 0 0 #FFFFFF, 0 20px 0 0 #FFFFFF;
  }
  .main-nav--closed .main-nav__toggle:hover::before {
    background-color: #D22856;
    box-shadow: 0 10px 0 0 #D22856, 0 20px 0 0 #D22856;
  }
  .main-nav--closed .main-nav__toggle:active::before {
    background-color: rgba(210, 40, 86, 0.3);
    box-shadow: 0 10px 0 0 rgba(210, 40, 86, 0.3), 0 20px 0 0 rgba(210, 40, 86, 0.3);
  }
  .main-nav--opened .main-nav__toggle {
    top: 10px;
    right: 0;
    left: auto;
  }
  .main-nav--opened .main-nav__toggle::before,
  .main-nav--opened .main-nav__toggle::after {
    content: "";
    position: absolute;
    top: 24px;
    left: 18px;
    width: 36px;
    height: 2px;
    background-color: #FFFFFF;
  }
  .main-nav--opened .main-nav__toggle::before {
    transform: rotate(45deg);
    box-shadow: none;
  }
  .main-nav--opened .main-nav__toggle::after {
    transform: rotate(-45deg);
  }
  .main-nav--opened .main-nav__toggle:hover::before,
  .main-nav--opened .main-nav__toggle:hover::after {
    background-color: #D22856;
  }
  .main-nav--opened .main-nav__toggle:active::before,
  .main-nav--opened .main-nav__toggle:active::after {
    background-color: rgba(210, 40, 86, 0.3);
  }
}
@media (max-width: 1200px) and (min-width: 660px) {
  .main-nav--opened .main-nav__toggle {
    top: 27px;
  }
}
@media (max-width: 660px) {
  .main-nav--closed .site-list__item {
    display: none;
  }
  .main-nav--closed .user-list__item {
    display: none;
  }
  .main-nav--nojs .main-nav__toggle {
    display: none;
  }
  .main-nav--nojs .main-nav__container {
    position: static;
    min-height: 0;
  }
  .main-nav--nojs .site-list__item {
    display: block;
  }
}
.main-nav--opened {
  background-color: #283645;
}
